.profile{
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    max-width: 100%;
    padding: 30px;
}

.profile-bottom{
    border-top: 1px solid #dbdbdb;
    width: 1000px;
    padding-top:25px;
    max-width: 100%;
}

.profile-bottom img{
    height: 100%;
    width: 30%;
    object-fit: fill;
    margin-left: 20px;
    margin-bottom: 10px;
}

.profile-inner{
    display: flex;
    flex-direction: column;
    width: 1000px;
    max-width: 100%;
}

.profile-left{
    width: 300px;
    max-width: 300px;
    display: flex;
    flex-direction: row;
    justify-content: center;
}

.profile-top{
    display: flex;
    flex-direction: row;
    align-items: center;
    margin-bottom: 25px;
}

.profile-right{
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    height: 150px;
}

.profile-right-second{
    display: flex;
    flex-direction: row;
    align-items: center;
}

.profile-right-second div{
    margin-right: 30px;
}

.profile-right-second div:nth-child(2):hover{
    cursor: pointer;
}

.profile-right-second div:nth-child(3):hover{
    cursor: pointer;
}

.profile-right-first button{
    width: 91px;
    height: 28px;
    border: none;
    border: 1px solid rgba(var(--d69,0,149,246),1);
    background-color: rgba(var(--d69,0,149,246),1);
    border-radius: 3px;
    text-align: center;
    color: white;
    font-size: 14px;
    font-weight: 600;
}

.profile-right-first button:hover{
    cursor: pointer;
}

.profile-right-third-name{
    display: flex;
    flex-direction: row;
    align-items: center;
}

.profile-right-first{
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-between;
}

.profile-right-first i{
    margin-left: 20px;
}


.followers-modal{
    width:400px;
    height:400px;
    background-color: rgba(var(--b3f,250,250,250),1);
    border-radius: 17px;
    outline: none;
    margin-top: 100px;
    margin-left:36.11vw;
    border: 1px solid #dbdbdb;
}

.followers-top{
    display: flex;
    flex-direction: row;
    align-items: center;
    border-bottom: 1px solid #dbdbdb;
}

.followers-top{
    height: 42px;
}

.followers-top div:nth-child(1){
    width: 48px;
}

.followers-top div:nth-child(2){
    width: 304px;
    text-align: center;
    font-size: 16px;
    font-weight: 600;
    justify-content: space-between;
}

.followers-top div:nth-child(3){
    width: 48px;
}

.followers-top div:nth-child(3):hover{
    cursor: pointer;
}

.followers-top div:nth-child(3) img{
    height: 24px;
    width: 24px;
}

.followers-list{
    display: flex;
    flex-direction: column;
    overflow-y: auto;
    padding-top: 15px;
    padding-left: 10px;
    padding-right: 10px;
}

.followers-result-item{
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: flex-start;
    margin-bottom: 15px;
}

.followers-result-item-img{
    margin-right: 15px;
    display: flex;
    align-items: center;
    justify-content: center;
}

.followers-result-item-name{
    display: flex;
    flex-direction: column;
    justify-content: center;
}

.followers-result-item-username{
    font-weight: 6000;
    font-size: 14px;
    color: black;
}

.followers-result-item-fullname{
    font-weight: 400;
    font-size: 14px;
    color: grey;
}

.followers-list a {
    text-decoration: none;
}

.settings-modal-inner{
    display: flex;
    background-color: rgba(var(--b3f,250,250,250),1);
    flex-direction: column;
    align-items: center;
    justify-content: center;
    height: 200px;
}

.settings-modal-inner div{
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;
    width: 600px;
    max-width: 100%;
    margin-top:7px;
    margin-bottom: 7px;
}


.settings-modal-inner div div{
    font-size: 17px;
    font-weight: 500;
}

.bio-input{
    width: 233px;
    height: 20px;
    padding-left:10px;
    padding-right:10px;
    border:none;
    outline: none;
    border: 1px solid #dbdbdb;
    margin-right: 20px;
    color:grey;
    text-overflow: ellipsis;
    font-size: 14px;
    border-radius: 3px;
}

.bio-input::placeholder{
    text-align: center;
}

.settings-username div input{
    width: 333px;
    height: 30px;
    padding-left:10px;
    padding-right:10px;
    border:none;
    outline: none;
    border: 1px solid #dbdbdb;
    margin-right: 20px;
    color:grey;
    text-overflow: ellipsis;
    font-size: 14px;
    border-radius: 3px;
}

.settings-fullname div input{
    width: 333px;
    height: 30px;
    padding-left:10px;
    padding-right:10px;
    border:none;
    outline: none;
    border: 1px solid #dbdbdb;
    margin-right: 20px;
    color:grey;
    text-overflow: ellipsis;
    font-size: 14px;
    border-radius: 3px;
}

.settings-bio div input{
    width: 333px;
    height: 30px;
    padding-left:10px;
    padding-right:10px;
    border:none;
    outline: none;
    border: 1px solid #dbdbdb;
    margin-right: 20px;
    color:grey;
    text-overflow: ellipsis;
    font-size: 14px;
    border-radius: 3px;
}

.settings-modal-inner div button{
    width: 80px;
    height: 32px;
    border:0;
    color: white;
    font-weight: 600;
    background-color: #0e96f6;
    border-radius: 3px;
}

.settings-modal{
    width:650px;
    max-width: 100%;
    margin-top:30vh;
    margin-left:30%;
    margin-right: 30%;
    height:200px;
    outline:none;
    border:1px solid #dbdbdb;
}

.settings-modal-profile{
}

.settings-modal-profile input{
    position: absolute;
    opacity: 0;
    width: 120px;
    height: 120px;
}

.settings-modal-profile input:hover{
    cursor: pointer;
}