.inp{
    position: absolute;
    opacity: 0;
    width: 30px;
    background-color: grey;
}
.inp-img{
    position: absolute;
    height: 30px;
    width: 30px;
}

.inp-outer:hover{
    cursor: pointer;
}

.inp:hover{
    cursor: pointer;
}


.main-post{
    margin-top: 10px;
}

.main-post video{
}

.opacity{
    opacity: 0;
}

.add-post-outer{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    background-color: rgba(var(--b3f,250,250,250),1);
    margin-top: 20px;
    max-width: 100%;
}

.add-post{
    display: flex;
    justify-content: flex-start;
    width: 654px;
    max-width: 100%;
    padding-top:15px;
    padding-bottom: 15px;
}
.post-filters-outer{
    width: 654px;
    border-bottom: 1px solid #dbdbdb;
    display: flex;
    flex-direction: column;
    border-top: 1px solid #dbdbdb;
    padding-top: 15px;
}

.post-filters-outer div{
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 20px;
    font-weight: 430;
    margin-bottom: 7px;
}

.create-post-button{
    margin-left:60px;
}

.create-post-button button{
    height: 30px;
    border: 0;
    background-color: rgb(255, 255, 255);
    font-size: 16px;
    border: 1px solid #dbdbdb;
    padding-left: 17px;
    padding-right: 17px;
}

.create-post-button button:hover{
    cursor: pointer;
}

.post-filters-inner{
    display: flex;
    overflow-x: auto;
    padding : 0;
    list-style-type: none;
    margin: 0;
}

.post-filters-inner::-webkit-scrollbar{
    width: 0;
}

.post-filters-inner li{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.post-filters-inner li div{
    font-size:18px;
    font-weight: 300;
}

.add-post-caption{
    display: flex;
    flex-direction: row;
    align-items: center;
    margin-left: 30px;
    margin-bottom: 10px;
}

.add-post-caption textarea{
    margin-right: 40px;
    border: 1px solid #dbdbdb;
    border-radius: 2%;
    color: grey;
    font-size: 17px;
    padding-left: 6px;
    padding-right:6px;
    height: 25px;
    width: 420px;
    outline: none;
    resize: none;
}

.add-post-caption textarea::placeholder{
    color: grey;
    text-align: center;
}

.nav-post{
    display: flex;
    align-content: center;
    justify-content: center;
    width: 654px;
    max-width: 100%;
    border-bottom: 1px solid #dbdbdb;
    padding-bottom: 20px;
}

.nav-post div{
    width: 400px;
    display: flex;
    flex-direction: row;
    justify-content: space-around;
    align-items: center;
}

.nav-post div div{
    font-size: 18px;
    font-weight: 300;
    width: 80px;
    padding-top:5px;
    padding-bottom:5px;
    border-bottom: 1px solid rgba(0, 0, 0, 0.71);
}

.selected{
    background-color:rgba(0, 0, 0, 0.71);
    color: white;
}

.not-selected {
    color:rgba(0, 0, 0, 0.71);
    background-color:white;
}

.nav-post div div:hover{
    cursor: pointer;
}

.loading-modal img{
    position: absolute;
    top: 50%;
    left: 50%;
    width: 120px;
    height: 120px;
    margin:-60px 0 0 -60px;
    -webkit-animation:spin 2s linear infinite;
    -moz-animation:spin 2s linear infinite;
    animation:spin 2s linear infinite;
}

@-moz-keyframes spin { 100% { -moz-transform: rotate(360deg); } }
@-webkit-keyframes spin { 100% { -webkit-transform: rotate(360deg); } }
@keyframes spin { 100% { -webkit-transform: rotate(360deg); transform:rotate(360deg); } }