.story-outer{
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;
    background-color: rgba(var(--b3f,250,250,250),1);
    margin-top: 20px;
    max-width: 100%;
}

.story{
    display: flex;
    flex-direction: row;
    overflow-x: auto;
    justify-content: flex-start;
    width: 614px;
    max-width: 100%;
    background-color: rgba(var(--b3f,250,250,250),1);
    padding-left: 20px;
    padding-right: 20px;
    padding-top: 16px;
    padding-bottom: 16px;
}

.story::-webkit-scrollbar{
    width: 0;
}

