.post-card-post{
    height: auto;
    width: 654px;

}

.post-card-outer{
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;
    background-color: rgba(var(--b3f,250,250,250),1);
    margin-top: 20px;
    max-width: 100%;
}


.post-card-top{
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-between;
    width: 654px;
    max-width: 100%;
    background-color: white;
    border-bottom: 1px solid #dbdbdb;
}

.post-card-top-first{
    display: flex;
    flex-direction: row;
    align-items: center;
}

.post-card{
    display: flex;
    flex-direction: column;
    width: 654px;
    max-width: 100%;
    border: 1px solid #dbdbdb;
    background-color: white;

}

.post-card-actions {
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-between;
    margin-top: 10px;
    margin-left: 15px
}
.post-card-actions img{
    margin-right: 25px;
    height: 25px;
    width: 25px;
}

.post-card-top-second img{
    margin-right: 25px;
    height: 25px;
    width: 25px;
}

.post-card-top-second img:hover{
    cursor: pointer;
}


.post-card-actions img:hover{
    cursor: pointer;
}

.post-card-bottom{
    display: flex;
    flex-direction: row;
    align-items: center;
    height: 30px;
    margin-left: 15px
}
.post-card-bottom .div{
    font-size: 10px;
}
.post-card-time{
    font-size: 10px;
    margin-left: 15px
}
.post-card-comments{
    margin-top: 10px;
    padding-top: 5px;
    padding-bottom: 5px;
    border-top: 1px solid #dbdbdb;
    display: flex;
    justify-content: space-between;

}
.post-card-comments textarea{
    border: none;
    outline: none;
    padding-left: 10px;
    padding-right: 10px;
    color: grey;
    width: 534px;
    max-width: 534px;
    margin-left: 25px;
    resize: none;
}

.post-card-comments textarea::placeholder{
    color: grey;
}

.post-card-actions-upvotes{
    display: flex;
    flex-direction: row;
}

.post-card-actions-upvotes img:hover{
    cursor: pointer;
}

.post-card-post figure{
    padding: 0;
    margin: 0;
}

.post-card-top-second textarea{
    margin-right: 25px;
    border: 1px solid #dbdbdb;
    border-radius: 2%;
    color: grey;
    font-size: 17px;
    padding-left: 6px;
    padding-right:6px;
    height: 19px;
    width: 200px;
    outline: none;
    resize: none;
}

.post-card-top-second textarea::placeholder{
    color: grey;
    text-align: center;
}

.post-card-all-comments{
    margin-left: 15px;
}

.post-card-all-comments-first{
    font-size: 12.5px;
    color: #8e8e8e;
}

.post-card-all-comments-first:hover{
    cursor: pointer;
}

.post-card-all-comments-second{
    margin-top:5px;
    display:flex;
    flex-direction: row;
    margin-bottom: 5px;
}

.post-card-all-comments-second-username{
    font-size: 13px;
    font-weight: 600;
    margin-right: 10px;
}

.post-card-all-comments-second-comment{
    font-size: 13px;
}

.comments-modal{
    margin-left:15.2778vw;
    margin-right: 15.2778vw;;
    width: 1000px;
    outline:none;
    margin-top: 12.1803vh;
    display: flex;
    padding:0;
    flex-direction: row;
}

.comments-modal-right{
    display: flex;
    margin-top: 20px;
    flex-direction: column;
    background-color: rgba(var(--b3f,250,250,250),1);
    border: 1px solid #dbdbdb;
    width: 335px;
}

.comments-modal-left{
    margin-top: 100px;
}

.comments-modal-inner{
    display: flex;
    flex-direction: row;
    margin-top: 10px;
    margin-left: 10px;
}

.comments-modal-inner-right{
    display: flex;
    flex-direction: column;
    justify-content: center;
}

.comments-modal-inner-top{
    display: flex;
    flex-direction: row;
}

.comments-modal-username{
    font-size: 13px;
    font-weight: 600;
    margin-right: 15px;
}

.comments-modal-comment{
    font-size: 13px;
}

.comments-modal-time{
    font-size: 10px;
    color: #8e8e8e;
}

