.story-card-username{
    overflow:hidden;
    text-overflow:ellipsis;
    font-size: 12px;
    max-width:60px;
    width:60px;
}
.story-card-username:hover{
    cursor: pointer;
}

.story-modal{
    padding: 0;
    margin: 0;
    border: none;
    outline: none;
    margin-top: 50px;
    margin-left: 36.1vw;
}

.story-modal-inner{
    display: flex;
    flex-direction: column;
}

.story-modal-inner-top{
    display: flex;
    width: 380px;
    max-width: 100%;
    align-items: center;
    padding-left: 10px;
    padding-top: 10px;
    z-index: 10;
}

.story-modal-inner-bottom{
    position: absolute;
    top:100;
    left: 36.1vw;   
}

.story-modal-inner-bottom:hover{
    cursor: pointer;
}

.story-modal-inner-left{
    z-index: 10;
}

.story-modal-inner-right{
    z-index: 10;
}

