.login-main-outer{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    max-width: 100%;
    height: 800px;
    max-height: 100%;
}

.login-main{
    display: flex;
    flex-direction: column;
    width:350px;
    max-width: 100%;
    min-width: 350px;
}

.login-inner-image img{
    width:175px;
    height: 51px;
    margin-top: 10px;
}

.login-inner{
    border: 1px solid #dbdbdb;
    border-radius: 3px;
    margin-bottom: 20px;
    background-color: white;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}

.login-inner-image{
    display: flex;
    align-items: center;
    justify-content: center;
    margin-bottom: 5px;
    padding-top: 10px;
    padding-bottom: 10px;
}

.login-inner-form{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    padding-bottom: 25px;
    /* border-bottom: 1px solid #dbdbdb; */
    width: 268px;
}

.login-inner-form input{
    width: 250px;
    height: 20px;
    border: 0;
    padding: 0;
    border: 1px solid #dbdbdb;
    background-color: rgba(var(--b3f,250,250,250),1);
    padding: 8px;
    outline: none;
    text-overflow: ellipsis;
    margin-top: 10px;
    color: grey;
    font-size: 14px;
    border-radius: 3px;
}

.login-inner-form button{
    width: 268px;
    height: 30px;
    margin-top: 20px;
    border:0;
    color: white;
    font-weight: 600;
    background-color: #0e96f6;
    border-radius: 3px;
}

.login-inner-second{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    margin-bottom: 20px;
}

.login-inner-third{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    margin-top:10px;
    margin-bottom: 20px;
}

.login-inner-fourth{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    margin-top:10px;
    margin-bottom: 20px;
}

.login-signup{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    border : 1px solid #dbdbdb;
    border-radius: 3px;
}

.login-signup{
    padding-top:15px;
    padding-bottom: 15px;
    background-color: white;
}

.login-signup a{
    color: #239ff7;
    font-weight: 600;
    text-decoration: none;
}

.settings-modal-profile{
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;
    max-width: 100%;
    margin-top:7px;
    margin-bottom: 7px;
}

.login-inner-second form button:hover{
    cursor: pointer;
}