.nav{
    display: flex;
    overflow-x: hidden;
    justify-content: space-around;
    align-items: center;
    border-bottom: 1px solid #dbdbdb;
}

.nav ul{
    display: flex;
    list-style-type: none;
}

.nav ul li{
    margin-left: 30px;
}

.search-outer{
    border: 1px solid #dbdbdb;
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 5px;
    border-radius: 3px;
    width: 362px;
    margin-bottom: 20px;
    background-color: rgba(var(--b3f,250,250,250),1);
}

.search{
    outline: none;
    color: grey;
    border: none;
    width: 362px;
    background-color: rgba(var(--b3f,250,250,250),1);
}


@media screen and (max-width:600px){
    .search{
        display: none;
    }
}

.search::placeholder{
    text-align: center;
    color: grey;
}

.search-modal{
    outline: none;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    margin-top: 60px;
    width: 372px;
    margin-left:37.08vw;
}

.search-main{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}

.search-result{
    height: 362px;
    display: flex;
    flex-direction: column;
    overflow-y: auto;
    border: 1px solid #dbdbdb;
    width: 352px;
    background-color: rgba(var(--b3f,250,250,250),1);
    max-width: 100%;
    padding-top: 15px;
    padding-left: 10px;
    padding-right: 10px;
    height: 372px;
    max-height: 100%;
    overflow-y: auto;
    border-radius: 3px;
}

.search-result-item{
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: flex-start;
    margin-bottom: 15px;
}

.search-result-item-img{
    margin-right: 15px;
    display: flex;
    align-items: center;
    justify-content: center;
}

.search-result-item-name{
    display: flex;
    flex-direction: column;
    justify-content: center;
}

.search-result-item-username{
    font-weight: 540;
    font-size: 15px;
    color: black;
}

.search-result-item-fullname{
    font-weight: 500;
    font-size: 14px;
    color: grey;
}

.search-result a {
    text-decoration: none;
}